body {
  margin: 0%;
  padding: 0%;
  overflow-x: hidden;
  overflow-x: scroll;
  background-color: rgb(0, 0, 0);
  background-repeat: no-repeat;
  user-select: none;

}

.title {
  position: fixed;

  color: white;
  text-shadow: 0px 0px 100px rgb(0, 255, 221), 0px 0px 1000px rgb(204, 0, 255);
  margin-top: 30px;
  background: linear-gradient(to left, #00ffcc, #dd56ff);
  -webkit-text-fill-color: transparent;
  -webkit-background-clip: text;
  text-align: center;
  z-index: 200;
  /* margin-left: 30vw; */
  font-size: 4.05vw;
  /* font-size: 3.8rem; */
  font-family: 'Orbitron', sans-serif;
}

