@keyframes pic {
  1% {
    transform: none;
    opacity: 1;
  }

  2% {
    opacity: 0.5;
  }

  3% {
    transform: none;
    opacity: 0.5;
  }

  4% {
    opacity: 0.5;

  }

  5% {
    opacity: 1;

  }

  7% {
    opacity: 0.6;

  }

  10% {
    opacity: 1;

  }

  13% {
    opacity: 0.5;

  }

  20% {
    opacity: 1;

  }

}

.a {

  position: fixed;
  margin-left: 24vw;
  background: linear-gradient(135deg, rgba(85, 85, 85, 0.1), rgba(255, 255, 255, 0));
  width: 60vw;
  /* filter: blur(0px); */
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  height: 150px;
  z-index: 100;
  border-radius: 10px;
  /* border:1px solid rgba(255, 255, 255, 0.18); */
  box-shadow: 0 8px 32px 0 rgba(0, 0, 0, 0.37);
}

.boxes {
  position: relative;
  /* animation-delay: 14s; */
  /* float: left; */
  background-repeat: no-repeat;
  background: linear-gradient(to bottom, rgb(3, 0, 20), rgb(33, 32, 32), rgb(61, 61, 61), rgb(129, 129, 129));
  width: 180px;
  height: 180px;
  z-index: 12;

  border-radius: 120px;
  margin-top: 20px;
  /* margin: 30vh 30px 0px 50px; */
  box-shadow: 0px 5px 30px -8px rgb(187, 54, 253), 0px -6px 30px -8px rgb(53, 226, 189);

  transition: all 0.6s ease-in-out;

}

.bubble {
  width: 176px;
  height: 176px;
  z-index: 10;
  margin-top: -180px;
  opacity: 0;
  border: 2px solid rgb(255, 255, 255);
  border-radius: 100px;
  background-color: #00ffcc00;
  border-radius: 1000px;
}

:hover.profile>.bubble {

  animation: spread 0.6s ease-out;
  /* border: 0px solid rgb(255, 255, 255); */
  /* opacity: 1; */
}


@keyframes spread {
  1% {
    transform: scale(1);
    opacity: 1;
  }



  100% {
    transform: scale(2);
    opacity: 0;
  }
}

.profile {
  align-items: center;
  justify-content: center;
  font-size: 1.2rem;
  display: flex;
  float: left;
  flex-direction: column;
  z-index: 10;
  /* border: 1px solid rgba(255, 255, 255, 0.5); */
  /*-webkit-background-clip: padding-box;  for Safari
  background-clip: padding-box;
*/

  width: 350px;
  height: 350px;
  margin: auto;
  margin: 18vh auto -10vh 76px;


  transition: all 0.6s ease-in-out;
  border-radius: 200px;
  text-align: center;
}


:hover.profile .links{
  scale: 1; 
}
