.scrollbar {
  /* background-color: rgb(255, 53, 53); */
  width: 500px;
  padding-top: 50px;
  height: 5220px;
  align-items: center;
  margin: auto;
  overflow-x: scroll;
  overflow-x: hidden;

}

::-webkit-scrollbar {
  width: 0px;
  height: 0px;
}

::-webkit-scrollbar-thumb {
  background: rgb(250, 250, 250);

}

.all_icons {
  /* margin-top: 5px; */
  float: left;
  position: fixed;
  width: 100%;
  height: 100%;
  z-index: -1;

  background-color: rgb(0, 0, 0);
}

@keyframes allicons {
  0% {
    margin-top: 0px;

  }

  50% {
    margin-top: 20px;
  }

  100% {
    margin-top: 0px;
  }

}

.icons {
  width: 30px;
