@media only screen and (max-width: 900px) {
  .title {
    margin-left: 0;
    font-size: 8vw;
    margin-right: 0;
  }

  .a {
    width: 100%;
    height: 20vh;
    margin-left: 0vw;
  }
}

@media only screen and (max-width: 450px) {
